*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Oswald', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
}

.uppercase {
  text-transform: uppercase;
}

section {
  margin-top: 40px;
}

a{
  text-decoration: none;
  color: inherit;
}

form {
  display: flex;
  align-items: center;
  flex-direction: column;
}

form > input {
  width: 240px;
  height: 40px;
  margin: 5px 20px;
  border-radius: 30px;
  border: none;
  padding: 7px;
  background-color: rgb(255, 177, 131)
}

form > input:focus {
  outline: none;
}

form > select {
  width: 240px;
  height: 40px;
  margin: 20px;
  border-radius: 30px;
  border: none;
  padding: 7px;
}

form > select:focus {
  outline: none;
}

input[type="file"] {
  height: 30px;
  width: 400px;
  background-color: transparent;
}

input[type="file"] button {
  background-color: red;
}
.app {
  text-align: center;
}

.navbar .logo {
  width: 150px;
  margin: 30px auto;

}

@media(min-width: 768px ) {
  .patient-view,
  .consultation-view{
    width: 500px;
    margin: 40px auto;
    padding-bottom: 20px; 
  }
}
.btn {
  font-family: 'Oswald', sans-serif;
  letter-spacing: 3px;
  color: #fff;
  border: none;
  cursor: pointer;
}

.btn.mt-0 {
  margin-top: 0;
}

.btn:focus{
  outline: none;
}

.btn-md {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 40px;
  padding: 0 40px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.btn-lg {
  max-width: 500px;
  width: 90vw;
  height: 50px;
  margin-top: 24px;
}

.btn-round {
  border-radius: 50px;
}

.btn-center {
  align-self: center;
}

.btn-primary {
  background-color: #FF8540;
  color: #fff;
  -webkit-transition: .2s ease-in-out;
  transition: .2s ease-in-out;
}

.btn-primary:hover {
  background-color: #00A779;
  color: #333;
}

.btn-white {
  background-color: #fff;
  color: #333;
  -webkit-transition: .2s ease-in-out;
  transition: .2s ease-in-out;
}

.btn-white:hover { 
  background-color: #FF8540;
  color: #fff;
}

.btn-cancel {
  background-color: #04346C;
  color: #fff;
  -webkit-transition: .2s ease-in-out;
  transition: .2s ease-in-out;
}

.btn-cancel:hover {
  background-color: #6899D3;
  color: #333;
}

.btn-delete {
  background-color: #dc143c;
  color: #fff;
  -webkit-transition: .2s ease-in-out;
  transition: .2s ease-in-out;
}

.btn-delete:hover {
  background-color: #333;
  color: #dc143c
}

.btn-transparent {
  background-color: transparent;
  color:#FF8540;
  -webkit-transition: .2s ease-in-out;
  transition: .2s ease-in-out;
}

.btn-transparent:hover {
  text-decoration: underline;
}

.btn-back {
  position: relative;
}

.btn-back::before {
  content: '\2190';
  font-size: 1.2em;
  margin-right: 4px;
}
.home {
  background-color: #fff;
  padding: 0 20px;
}

.logo {
  max-width: 461px;
  width: 90vw;
  margin: 80px auto 30px;
}

.logo img {
  width: 100%;
}

.buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.buttons .btn-primary:hover,
.signup .btn-primary:hover,
.login .btn-primary:hover {
  background-color: #00A779;
}

.signup ::-webkit-input-placeholder,
.login ::-webkit-input-placeholder {
  color: #666;
}

.signup ::-moz-placeholder,
.login ::-moz-placeholder {
  color: #666;
}

.signup :-ms-input-placeholder,
.login :-ms-input-placeholder {
  color: #666;
}

.signup ::-ms-input-placeholder,
.login ::-ms-input-placeholder {
  color: #666;
}

.signup ::placeholder,
.login ::placeholder {
  color: #666;
}

.signup input,
.login input {
  background-color: rgb(255, 177, 131);
}

.signup a > button,
.login a > button {
  margin: 0;
}


.container {
  position: fixed;
  top: 50px;
  left: 0;
  width: 100vw;
  display: flex;
  flex-direction: column;
  background: #35D3A7;
  opacity: 0.95;
  color: #333;
  z-index: 2;
  -webkit-transition: height 0.3s ease 0s;
  transition: height 0.3s ease 0s;
}

.menu-list {
  padding-top: '3rem',
}

.navbar .user {
  color: #fff;
  font-family: 'Oswald', sans-serif;
}

.user span{
  color: #333;
}

@-webkit-keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes slideIn {
  0% {
    -webkit-transform: translateX(-2%);
            transform: translateX(-2%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slideIn {
  0% {
    -webkit-transform: translateX(-2%);
            transform: translateX(-2%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes shrink {
  0% {
    width: 95%;
  }
  100% {
    width: 90%;
  }
}

@keyframes shrink {
  0% {
    width: 95%;
  }
  100% {
    width: 90%;
  }
}
.profile {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 80px;
}

.profile form {
  display: flex;
  flex-direction: row;
  position: relative;
  margin-bottom: 50px;
}

.profile form i {
  position: absolute;
  right: 40px;
  color: #006D4F;
}

.profile .patients-list {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}

.profile .btn {
  margin: 0;
}

.profile .patients-list .btn-white {
  height: 40px;
  margin: 7px 0;
  width: 100%;
  padding: 0 30px
}

.patient-view,
.add-patient {
  margin-top: 80px;
}
.patient-view .patient-data {
  background-color: #fff;
  margin: 0 15px;
  border-radius: 20px;
  padding: 30px 10px;
  display: flex;
  flex-direction: column;
  color: #333;
}

.patient-view h2 {
  font-size: 1.2em;
  text-align: left;
  margin-bottom: 25px; 
  font-weight: 200;
}

.patient-view h3,
.patient-view h4  {
  font-size: 1em;
  margin-bottom: 5px;
  font-weight: 400;
}

.patient-view h4 {
  color: #52897B
}

.patient-data .basic-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  border-bottom: 1px solid #8492A6;
}

.patient-data .basic-info .age-gender {
  display: flex;
}

.patient-view .patient-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  border-bottom: 1px solid #8492A6;
  color: #333;
}

.patient-view .more-info {
  margin-top: 10px;
  color: #FF686B;
}

.profile .btn {
  margin: 0;
}

.patient-view .consultation-list .btn-white {
  height: 40px;
  margin: 7px 0;
  width: 100%;
  padding: 0 30px
}

.add-patient form input,
.edit-patient form input,
.add-consultation form input {
  background-color: transparent;
  border-bottom: 1px solid #00A779;
  border-radius: 0;
  padding: 0;
  height: auto;
  margin-bottom: 20px;
  color: #333;
}

::-webkit-input-placeholder {
  color: #333;
}

::-moz-placeholder {
  color: #333;
}

:-ms-input-placeholder {
  color: #333;
}

::-ms-input-placeholder {
  color: #333;
}

::placeholder {
  color: #333;
}

.add-patient textarea,
.edit-patient textarea,
.add-consultation textarea {
  width: 245px;
  height: 80px;
  background-color: transparent;
  border: 1px solid #00A779;
  margin-bottom: 20px;
  font-size: 12px;
  padding: 5px;
  color: #333
}

/*******CONSULTATIONS*********/
.consultation-view {
  padding: 0 10px 20px;
}

.consultation-view .consult-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  border-bottom: 1px solid #8492A6;
  color: #333;
  background-color: #fff;
  padding: 5px 10px;
}

.consult-row h3 {
  font-weight: 400
}

.consult-row h4 {
  color: #52897B;
  letter-spacing: 1px;
}

.consult-row.conduct {
  flex-direction: column;
}

.consult-row.conduct .text {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.consult-row.conduct .btn-md {
  margin: 25px 0 10px;
  height: 30px;
  width: 80px;
  padding: 0;
}

.consult-row.conduct h4 {
 text-align: right;
}

.consult-form{
  background-color: #52897B;
  border-radius: 7px;
  margin-bottom: 15px;
  padding: 20px 0;
  color: #fff;
}

.consult-form textarea {
  background-color: transparent;
  border: #ccc solid 1px;
  width: 80%;
  color: #fff;
  padding: 10px;
  margin-top: 10px;
}

.exams {
  display: flex;
  flex-direction: column;
}
.exams .title {
  display: flex;
  flex-direction: column;
}

.exams .title h3 {
  text-align: left;
}

.exams .title .exams-form {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.exams .title .exams-form input {
 margin: 10px 0;
}

.exams .exams-form .btn-add-exam {
  margin: 10px 0;
  padding: 7px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content; 
}

.exams .exam-img {
  width: 150px;
}

.exams .exam-img img {
  width: 100%;
}

/*******ADD CONSULTATIONS*********/
.add-consultation .fisic-exam {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 80px auto 20px;
}

.add-consultation .radio {
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  padding: 20px 4px;
  border: 1px solid #00A779;
  max-width: 400px;
  width: 70vw; 
}

.add-consultation .radio p {
  align-self: center;
}

.add-consultation .radio label {
  color: #52897B;
  font-weight: 400;
  width: 30%;
}

.add-consultation .radio input {
  border: none;
    margin-bottom: 5px;
    border-bottom: 1px solid #00A779;
}

.add-consultation

.add-consultation .options {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: left;
  margin-top: 12px;
}

.add-consultation textarea.cid-name {
  height: 40px;
}

.add-consultation input[type="text"] {
  border: none;
  border-bottom: 1px solid #00A779
}
.textinput{  
  margin: 0 auto;  
}
.scheduling {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  overflow-x:auto;
}

.scheduling .date-today h2 {
  font-size: 2em;
  font-weight: 400;
}

.scheduling .date-today p {
  letter-spacing: 2px;
}

.scheduling .date-today p span {
  color: #52897B;
  font-weight: 600;
  font-size: 1.5em;
}

.scheduling table {
  border-spacing: 0;
  width: 100%;
}

.scheduling th, td {
  font-size: 1.5em;
  padding: 5px 10px;
  color: #333;
  text-align: center;
  padding: 8px;
}

.scheduling td {
    color: #52897B;
}

.scheduling tr:nth-child(even) {
  background-color: rgb(255, 232, 219)
}

.scheduling td .btn {
  width: 75px;
  height: 35px;
  padding: 0;
}

.scheduling .add-scheduling {
  margin-top: 20px;
}
.account {
  margin-top: 80px;
}

.account .user-data {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: left;
  margin: 0 auto;
}

.account h2 {
  font-size: 2em;
  font-weight: 400;
  margin-bottom: 30px;
}

.account .user-data h3 {
  font-weight: 400;
}

.account .user-data h3 span {
  font-weight: 400;
  letter-spacing: 1px;
  color: #52897B;
  margin-left: 5px;
}

/**********EDIT ACCOUNT***********/
.edit-account {
  margin-top: 80px;
}

.edit-account form input {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #00A779;
  border-radius: 0;
  padding: 0;
  height: auto;
  margin-bottom: 20px;
  color: #333;
}

.edit-account h2 {
  font-weight: 400;
  font-size: 2em;
  margin-bottom: 20px;
}

.edit-account h3 {
  font-weight: 400;
  font-size: 1.5em;
  margin-bottom: 0;
}

.edit-account p {
  font-size: .8em;
  margin-bottom: 15px;
}

.edit-account form input[name="cellphone"] {
  margin-bottom: 40px;
}

